    color: #3b76be; }

.btn {
  display: inline-block;
  background-color: #ffffff;
  padding: 0.625rem 2.5rem;
  color: #4a4a4a;
  text-align: center;
  text-decoration: none;
  border: 2px solid #ffffff; }
  .btn:hover, .btn:focus {
    background-color: transparent;
    color: #4a4a4a; }
  .btn--dark {
    background-color: #4a4a4a;
    color: #ffffff;
    border-color: #4a4a4a;
    border-radius: 4px; }
    .btn--dark:hover, .btn--dark:focus {
      color: #4a4a4a; }
  .btn--pink, .btn--red {
    background-color: #ffffff;
    color: #4a4a4a;
    border: 1px dashed #ed515b; }
    .btn--pink:hover, .btn--pink:focus, .btn--red:hover, .btn--red:focus {
      background-color: #ed515b;
      color: #ffffff;
      border-color: #ffffff; }
  .btn--white {
    background-color: transparent;
    color: #ffffff;
    border: 1px solid #ffffff;
    border-radius: 4px; }
    .btn--white:hover, .btn--white:focus {
      background-color: #ffffff;
      color: #4a4a4a; }
  .btn--solid-red {
    background-color: #ed515b;
    color: #ffffff;
    border-color: #ed515b; }
    .btn--solid-red:focus, .btn--solid-red:hover {
      background-color: #3b76be;
      color: #ffffff;
      border-color: #3b76be; }
  .btn--blue {
    background-color: #0f7090;
    color: #ffffff;
    font-size: 1.125rem;
    border-color: #0f7090;
    border-radius: 4px; }
    .btn--blue:focus, .btn--blue:hover {
      background-color: #0a4c62;
      color: #ffffff;
      border-color: #0a4c62; }

